<style lang="less" scoped>
.el-card{
    width: 100%;
    height: 100%;
    position: relative;
    .item{
        font-weight: bold;
        font-size: 20px;
        text-align: center;
        margin-bottom: 30px;
        cursor: pointer;
    }
    .current {
      position: relative;
      color: #0790ff;
      &:after {
        content: ' ';
        width: 56px;
        height: 4px;
        background: #0790ff;
        position: absolute;
        bottom: -7px;
        border-radius: 2px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .el-pagination{
      margin-top: 40px;
      margin-left: 200px;
  }
}
.j-c-s{
    display: flex;
